<template>
  <div>
  <p v-if="$store.state.search.length > 0" style="text-align:left;font-size:20px;margin-bottom:20px;color:#6baeec"><strong>You Searched For:</strong> {{$store.state.search}}</p>
	<div class="row">
	  <div class="fade-in col-lg-6 col-md-12" v-for="person in filteredlist">
      <a href="#/profile" v-on:click="updateuser(person.id)">
    		<card>
    			<p class="card-text">
    				<img style="float:left;margin-right:20px" height="150" width="150" :src="valimage(person)">
    				<h4 class="card-title">{{person.Name}}</h4>
    				<p style="color:#657bf4;margin:0">Class of {{person.Batch}}<br></p>
    				{{person['Department and Program']}}<br>
    				IITK Roll No.: {{person['Roll Number']}}
    			</p>
    	  </card>
		  </a>
    </div>
  </div>
  <p v-if="filteredlist.length == 0" style="text-align:center;font-size:30px;">No Results Found...</p>
	<div class="row">
	<div class="col-lg-12 col-md-6">
	<paginate
		v-model="$store.state.page"
		:page-count="totalpages"
		:page-range="3"
		:margin-pages="0"
		:click-handler="clickCallback"
		:prev-text="'<'"
		:next-text="'>'"
		:container-class="'pagination pagination-sm pagination-pills justify-content-center'"
		:page-class="'page-item'"
		:page-link-class="'page-link'"
		:prev-class="'page-item'"
		:prev-link-class="'page-link'"
		:next-class="'page-item'"
		:next-link-class="'page-link'"
		:hide-prev-next="true">
	</paginate>
	</div>
	</div>
  </div>
</template>
<style>
.pagination-pills .page-item .page-link {
  border-radius: 50rem !important;
  margin-left: 0.25rem;
  line-height: 1;
}

.pagination-pills .page-item:first-child .page-link {
  margin-left: 0;
}
</style>
<script>
	import firebase from "firebase";

	export default {
		data() {
			return {
				totalpages: 1,
			}
		},
		methods: {
			updateuser (id) {
				this.$store.commit('curr_user', id)
			},
			valimage (person) {
				if (person.img){
					return person.img;
				}else{
					return 'img/def_user.jpg'
				}
			},
			notif () {
				this.$notify({type: 'danger', horizontalAlign: 'center', message: 'This Portal is still under construction!'})
			},
			clickCallback (pageNum) {

			},
			loadData () {
				if(!this.$store.state.dataloaded) {
					this.$store.commit('load', true);
					var rawdata = [];
					var _this = this;
					const db = firebase.firestore();
					db.collection('career_portal').doc('data').get().then(function(doc) {
						if (doc.exists) {
							const fetched = doc.data();
							rawdata = Object.keys(fetched).map(key => {
								return fetched[key];
							});
							_this.$store.commit('set_users', rawdata);
						}
					});
					this.$store.commit('load', false);
				}
			},
		},
		computed: {
			filteredlist() {
				var userlist = this.$store.state.users;
				var toSearch = this.$store.state.search;
        var yearlist = this.$store.state.yearsSelected;
        var filtered = userlist.filter(m => (m.Name.toLowerCase().indexOf(toSearch.toLowerCase()) > -1 || m.Batch.toString().indexOf(toSearch.toLowerCase()) > -1));
        var temp = [];
        var flag = 0;
        this.$store.commit('toggleFilter', false);
        for (const [key, value] of Object.entries(this.$store.state.soi)) {
          if (value) {
            flag = 1;
            this.$store.commit('toggleFilter', true);
            const filter1 = filtered.filter(m => (m['Sector of Interest'].toLowerCase().indexOf(key) > -1));
            if (temp.length == 0) {
              temp = filter1;
            } else {
              temp = [...new Set([...filter1, ...temp])];
            }
          }
        }
        if (flag) {
          filtered = temp;
        }
        temp = [];
        flag = 0;
        if (yearlist) {
          for (var i in yearlist) {
            this.$store.commit('toggleFilter', true);
            const filter1 = filtered.filter(m => (m.Batch == yearlist[i]));
            if (flag == 0) {
              temp = filter1;
              flag = 1;
            } else {
              temp = [...new Set([...filter1, ...temp])];
            }
          }
        }
        if (flag) {
          filtered = temp;
        }
        temp = [];
        if (this.$store.state.linkedin) {
          this.$store.commit('toggleFilter', true);
          filtered = filtered.filter(m => (m['LinkedIn Profile'].length > 0));
        }
        var num = filtered.length;
				this.totalpages = Math.ceil(num/12);
				var toShow = [];
				var init = this.$store.state.page - 1;
				for(var i = (init)*12; (i < (init + 1)*12) && (i < num); i++){
					toShow.push(filtered[i]);
				}
				return toShow
			},
      yearWatcher() {
        return this.$store.state.yearsSelected;
      },
      degWatcher() {
        return this.$store.state.degreesSelected;
      },
      deptWatcher() {
        return this.$store.state.departmentsSelected;
      },
		},
		created: function(){
			this.loadData();
			this.notif();
		},
    watch: {
      yearWatcher(value) {
        this.$store.commit('reset_page');
      },
      degWatcher(value) {
        this.$store.commit('reset_page');
      },
      deptWatcher(value) {
        this.$store.commit('reset_page');
      },
    }
	}
</script>
